.card{
  margin: 0.5%;
  /* transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1); */
  background-color: #fff;
  width: 23%;
  /* height: 100%; */
  /* border-radius: 12px; */
  /* box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1); */
  float: left;
  border: 2px solid #fff;
  background-color: #fff;
  overflow: auto;
  border: 0.5px solid black;
}

.card__img{
  width: 100%;
  height: 100%;
  /* max-height: 280px; */
  /* border-radius: 12px; */
}

@media (max-width: 1024px) {
  .card{
    float: none;
    margin: 5%;
    width: 50%;
    flex-basis: 40%;
  }
}
.card__info{
  margin: 6px;
  /* border-radius: 12px; */
  letter-spacing: 2px;
  font-weight: 500;
  text-align: center;
}
