    font-family: 'Roboto', sans-serif;
}
/* @import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap'); */
.Lesions_wrapper__2ucMf{
    margin: 0;
    display: grid;
    grid-template-columns: 80% auto;
    grid-template-rows: 15% auto;
    grid-template-areas: 
        "head head"
        "container container";
    font-family: 'Ubuntu', sans-serif;
    grid-gap: 3px;
}

.Lesions_img__1wUXZ{
    display: inline-block;
    width: 100%;
    height: 100%;
}

.Lesions_items__1tVcc{
    
    /* display: grid; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0px 0px;
    padding: 0px 0px;
    -webkit-flex-flow: column wrap;
            flex-flow: column wrap; 
     

    
}

.Lesions_title__36Sh0{
    background-color: #fff;
    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;
    border-top-right-radius: 12px;
    border-bottom-right-radius: 12px;
    padding: 1px 1px 1px 1px;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    margin-top: 2.5%;
}

#Lesions_head__p8WeE{
    grid-area: head;
    font-size: 2rem;
    text-align: center;
    margin-bottom: 5px;
    padding: 5px 5px 5px 5px;
    letter-spacing: 2px;
    font-weight: 500;
    margin-top: 2.5%;
    /* margin-bottom: 1.5%; */
    font-family: "Quicksand";
    /* border-bottom: 1px solid #e2e2e2; */
}

#Lesions_container__2eBXY{
    grid-area: container;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    min-height: 70vh;
    max-height: auto;
}

#Lesions_form__1qAn5{
    grid-area: form;
    box-shadow: -11px -2px 42px -7px rgba(0, 0, 0, 0.1);
    border-left: 2px solid black;
    padding: 5%;
}

@media only screen and (max-width: 1024px){
    .Lesions_wrapper__2ucMf{
        grid-template-columns: 1fr;
        grid-template-rows: 0.4fr 2fr 1fr;
        grid-template-areas: 
        "head"
        "container"
        "form";
    }
    #Lesions_container__2eBXY{
        height: auto;
    }
}

.Lesions_title__36Sh0{
    font-size: 16px;
}

